:root {
  --primaryDkBlue: hsl(208, 100%, 34%);
  --primaryMedBlue: hsl(208, 51%, 67%);
  --primaryLtBlue: hsl(200, 89%, 85%);
  --primaryYellow: hsl(49, 100%, 50%);
  --primaryYellow_60: hsl(49, 100%, 50%, 0.6);
  --primaryYellow_80: hsl(49, 100%, 50%, 0.8);
  --primaryYellow_90: hsl(49, 100%, 50%, 0.9);
  --primaryDkYellow: hsl(60, 100%, 38%);
  --primaryDkGreen: hsl(124, 45%, 47%);
  --primaryGreen: hsl(92, 51%, 51%);
  --primaryOrange: hsl(32, 94%, 60%);
  --primaryDkOrange: hsl(23, 73%, 43%);
  --primaryRed: hsl(347, 100%, 45%);
  --primaryGrey: hsl(0, 0%, 50%);
  --primaryGreyDk: hsl(0, 0%, 23%);
  --primaryGreyMed: hsl(0, 0%, 60%);
  --primaryGreyLt: hsl(0, 0%, 80%);
  --weBlue: hsl(196, 100%, 47%);
  --darkBlue: hsl(218, 61%, 31%);
  --black: hsl(340, 14%, 4%);
  --blackMed: hsl(340, 3%, 23%);
  --blackLt: hsl(0, 1%, 62%);
  --textGrey: hsl(0, 0%, 44%);
  --footerGrey: hsl(0, 0%, 81%);
  --slateGrey: hsl(210, 13%, 50%);
  --dkSlateGrey: hsl(180, 25%, 25%);
  --dkGray: hsl(0, 0%, 41%);
  --ltGray: hsl(0, 0%, 83%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif;
  --font-heading: "Georama", Helvetica, Arial, sans-serif;
}

@keyframes no-transform {
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pulse {
  0% {
    opacity: 1;
    transform: none;
  }
  50% {
    opacity: 0.8;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
  display: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-text);
  color: var(--textGrey);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}
body.noscroll {
  overflow: hidden;
}

.container {
  width: 80%;
  width: min(90%, 75em);
  margin-inline: auto;
}
.container__content {
  width: 60%;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .container__content {
    width: 80%;
  }
}
@media (max-width: 43.74875em) {
  .container__content {
    width: 100%;
  }
}

@media (max-width: 59.999375em) {
  .row {
    padding: 0 1.25rem;
  }
}

.transport {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1.25rem;
  width: 60%;
  margin-bottom: 3.75rem;
}
@media (max-width: 59.999375em) {
  .transport {
    width: 100%;
  }
}
@media (max-width: 43.74875em) {
  .transport {
    flex-direction: column;
  }
}
.transport__caption {
  font-family: var(--font-text);
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.2;
  color: var(--primaryDkBlue);
  padding: 0.625rem 1.5625rem 1.875rem;
}
@media (max-width: 43.74875em) {
  .transport__caption {
    font-size: 1.25rem;
    padding: 0.625rem 0.9375rem 1.875rem;
  }
}

.air,
.iceRoad {
  width: 100%;
  border: 0.125rem solid var(--primaryDkBlue);
}

.air > figure,
.iceRoad > figure {
  margin: 0;
}

.cta {
  padding: 2.5rem 0;
  background-color: var(--primaryYellow);
  margin-bottom: 3.75rem;
}
.cta__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 65%;
}
@media (max-width: 59.999375em) {
  .cta__text {
    width: 100%;
  }
}
.cta__pullBanner {
  font-family: var(--font-heading);
  font-size: 2.75rem;
  color: var(--primaryDkBlue);
  margin: 0 auto 2.5rem;
  text-align: center;
}
@media (max-width: 43.74875em) {
  .cta__pullBanner {
    font-size: 2.25rem;
  }
}

.section-divider {
  border: 0.0625rem solid var(--primaryMedBlue);
  margin: 5rem auto 3.75rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-fs {
  justify-content: flex-start;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-ai-fs {
  align-items: flex-start;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-r {
  flex-direction: row;
}
.flex-fd-c {
  flex-direction: column;
}
.flex-fw-w {
  flex-wrap: wrap;
}
.flex-fw-nw {
  flex-wrap: nowrap;
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-family: var(--font-heading);
  line-height: 1.1;
}

h1 {
  font-size: 4.6875rem;
}

h2 {
  font-size: 3.625rem;
}

h3 {
  font-size: 2.75rem;
}

h4 {
  font-size: 2.125rem;
}

h5 {
  font-size: 1.75rem;
}

h6 {
  font-size: 1.375rem;
}

p {
  font-family: var(--font-text);
  color: var(--textGray);
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

button .button {
  text-align: center;
  display: inline-block;
  cursor: pointer;
  font-size: 1.125rem;
}
button .button a {
  color: white;
}
button .button a:hover {
  color: var(--primaryDkBlue);
  transition: all 200ms ease-in-out;
}

.btn {
  font-family: var(--font-heading);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  letter-spacing: 0.0625rem;
  font-size: 1.125rem;
}
.btn__cta, .btn__contact {
  color: white;
}
.btn a:hover {
  color: var(--primaryDkBlue);
  transition: all 200ms ease-in-out;
}
.btn--lc {
  text-transform: lowercase;
  font-weight: 300;
}
.btn--white {
  background-color: var(--white);
  border: 1px solid var(--primaryDkBlue);
  color: var(--primaryDkBlue);
  font-weight: 700;
}
.btn--white:hover, .btn--white:active, .btn--white:focus {
  background-color: var(--primaryDkBlue);
  color: var(--white);
  border: 0.0625rem solid var(--white);
  transition: all 300ms ease-in-out;
}
.btn--dark {
  background-color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  color: var(--white);
  font-weight: 700;
}
.btn--dark:hover, .btn--dark:active, .btn--dark:focus {
  background-color: var(--white);
  color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
}
.btn--dark-footer {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0.625rem 3.5rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  display: block;
}
.btn--yellow {
  background-color: var(--primaryYellow);
  border: 0.0625rem solid var(--primaryYellow);
  color: var(--primaryDkBlue);
  font-weight: 700;
}
.btn--yellow:hover, .btn--yellow:active, .btn--yellow:focus {
  background-color: var(--white);
  color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryYellow);
  transition: all 300ms ease-in-out;
}

.nav__item--btn,
.footer__item--btn {
  margin-left: 2.1875rem;
  background-color: transparent;
  border: none;
}
@media (max-width: 59.999375em) {
  .nav__item--btn,
.footer__item--btn {
    margin-left: 0px;
  }
}

header {
  background-color: var(--primaryYellow);
  width: 100%;
  position: absolute;
  z-index: 900;
}
header__logo img {
  padding: 0.625rem 0;
}
header.open .header__menu > span:first-child {
  transform: rotate(45deg);
}
header.open .header__menu > span:nth-child(2) {
  opacity: 0;
}
header.open .header__menu > span:last-child {
  transform: rotate(-45deg);
}
header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--primaryYellow);
  z-index: 400;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 400;
  padding: 0.625rem 0 0.3125rem;
  background-color: var(--primaryYellow);
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 0.625rem 0;
  }
}
.nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 59.999375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__toggle {
  width: 2.25em;
  margin-right: 0.625em;
  position: relative;
  cursor: pointer;
}
.nav__toggle > span {
  display: block;
  width: 1.625em;
  height: 0.125em;
  background-color: var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.nav__toggle > span:not(:last-child) {
  margin-bottom: 0.3125em;
}
.nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
  width: 100%;
}
@media (min-width: 43.75em) {
  .nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
    width: 100%;
    opacity: 1;
    visibility: visible;
    display: flex;
    flex-direction: column;
    min-width: 15.625rem;
    position: absolute;
    left: 100%;
    transform: translateX(-75%);
    margin-top: 1.875rem;
    list-style-type: none;
    padding-top: 0.9375rem;
  }
}
.nav__item a {
  font-family: var(--font-heading);
  font-size: 1rem;
  color: var(--primaryDkBlue);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.0625rem;
  cursor: pointer;
  position: relative;
  margin-left: 1.875em;
  transition: color 0.2s ease-out;
  padding: 1rem 0;
}
@media (max-width: 59.999375em) {
  .nav__item a {
    margin-left: 0px;
  }
}
.nav__item a::after {
  border-radius: 0.0625em;
  border-top: 0.1875em solid var(--white);
  content: "";
  position: absolute;
  right: 100%;
  bottom: -em(8);
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}
.nav__item a:hover {
  color: var(--white);
  text-decoration: none;
}
.nav__item a:hover::after {
  animation: anchor-underline 2s cubic-bezier(0, 0.5, 0, 1) infinite;
  border-color: var(--white);
}
.nav__item--lc {
  font-weight: 400;
  text-transform: none;
}

.submenu {
  display: none;
  justify-content: center;
  padding-left: 0;
  width: 100%;
}
@media (min-width: 60em) {
  .submenu {
    display: none;
    justify-content: center;
    padding-left: 0;
    align-items: center;
    text-align: center;
    position: absolute;
    background-color: var(--primaryYellow);
    padding: 0;
    top: 1.5625em;
  }
}
.submenu li {
  margin: 0 0 0.625rem;
  border-top: 0.0625rem solid var(--white);
}
.submenu li:first-child {
  border-top: none;
}

@media (max-width: 59.999375em) {
  .submenu-active .submenu {
    display: block;
    background-color: var(--primaryYellow);
    list-style-type: none;
  }
}

.subitem {
  margin: 0;
  text-align: left;
  color: var(--white);
  width: 100%;
  margin: 0;
  padding: 0.5rem 0 0 1.25rem;
}
@media (max-width: 59.999375em) {
  .subitem {
    padding-left: 0;
  }
}
.subitem:hover, .subitem:focus {
  background-color: var(--primaryYellow);
}
.subitem:last-child {
  padding-bottom: 0.75em;
}
.subitem a {
  margin: 0;
  text-align: left;
  padding: 0;
  text-transform: none;
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
}
.has-submenu ul.submenu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .has-submenu ul.submenu {
    opacity: 1;
    visibility: visible;
  }
}

.nav-open {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 4.6875em;
  text-align: center;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 1.25em;
  padding-left: 0;
  padding-bottom: 1.25em;
  z-index: 400;
}

.chevron-down {
  font-size: 1.125rem;
  font-weight: 700;
}

li.nav__item:first-child a {
  margin-left: 0;
}

.nav__item.has-submenu.submenu-active {
  background-color: transparent;
}

.page__headBkgd {
  width: 100%;
  background-color: var(--primaryDkBlue);
}
.page__heading {
  font-size: 4.6875rem;
  font-family: var(--font-heading);
  color: var(--white);
  letter-spacing: 0.0625rem;
  width: 60%;
  text-align: center;
  padding: 1.25rem 0;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .page__heading {
    font-size: 3.75rem;
    width: 100%;
    padding: 1.25rem 0;
  }
}
@media (max-width: 43.74875em) {
  .page__heading {
    font-size: 3.25rem;
  }
}
.page__intro {
