}
.customers__include__list {
  margin: 0;
}
.customers__include--list li {
  color: var(--white);
  margin-bottom: 0.5rem;
}

footer {
  min-height: 19.6875rem;
  background-color: var(--footerGrey);
}

.footer {
  padding-top: 2.5rem;
}
.footer__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-height: 21.875rem;
}
@media (max-width: 59.999375em) {
