}
@media (max-width: 43.74875em) {
  .copyright {
    padding-bottom: 1.25rem;
  }
}

.legal a {
  color: var(--primaryDkBlue);
}
.legal a:hover {
  color: var(--white);
  text-decoration: underline;
}

.hero__index {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  margin: 0;
  overflow: hidden;
  padding: 0;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .hero__index {
    transition: all 350ms ease-in-out;
    margin-bottom: 2.5rem;
  }
}
.hero__index--headline {
  font-size: 4.6875rem;
  color: var(--white);
